<script>
var requestOptions = {
  method: 'GET',
  redirect: 'follow'
};
export default {
  name: 'HomeView',
  data() {
    return {
      activities: []
    }
  },
  created() {
    fetch("https://api.github.com/repos/LiusCraft/MinecraftToolsWebsite/commits", requestOptions)
      .then(response => response.text())
      .then(result => this.activities = JSON.parse(result))
      .catch(error => console.log('error', error));
  }

}
</script>
